import React from 'react';
import './ListItems.css';

function ListItems(props) {
    // store the props.items in an array called items
    var items;
    // filter the items array according to the selected view
    if (props.view == "all") {
        items = props.items;
    } else if (props.view == "active") {
        items = props.items.filter(item => 
            !item.isCompleted)
    } else if (props.view == "completed") {
        items = props.items.filter(item =>
            item.isCompleted)
    }

    /* deconstruct the items list using the map function
    and create a new div for each item */
    const listItems = items.map(item => {
        return <div 
            key={item.key} 
            className={`list ${props.isDark ? "dark_list" : ""}`}>
            <div 
                className="circle_border_wrap">
                <div 
                    // when the circle is clicked isCompleted is toggled in state
                    onClick={() => props.toggleCompleted(item.key)}
                    /* if isCompleted is false we add the class .untick_circle to
                    to cover up the div with the tick, ie un-tick the item */
                    className={`${item.isCompleted ? "tick_circle" : "untick_circle"}
                                ${props.isDark ? "dark_untick_circle" : ""}`}>
                </div>
            </div>
            <p 
                className={`${item.isCompleted ? "crossover_text" : ""} 
                            ${item.isCompleted && props.isDark ? "dark_crossover_text" : ""}`}>
                {item.text}
            </p>
            <div 
                className="item_cross"
                // a function to delete the item if the cross is clicked
                onClick={() => props.deleteItem(item.key)}>
            </div>
        </div>
    })
    

    // display the deconstructed list using return
    return (
        <div id="items_container">{listItems}</div>
    )
}

export default ListItems;