import React from 'react';
import logo from './logo.svg';
import './App.css';
import ListItems from './ListItems';

class App extends React.Component {
  constructor(props) {
    super(props);

    // define the state
    this.state = {
      // an option to choose light or dark mode
      isDark: false,
      // an option to choose how which items to view from the list
      view: 'all',
      // the array of todos
      items: [],
      // the current todo being typed in the input field
      currentItem: {
        text: '',
        key: '',
        isCompleted: false
      }
    };

    // bind the methods to "this"
    this.handleInput = this.handleInput.bind(this);
    this.addItem = this.addItem.bind(this);
    this.deleteItem = this.deleteItem.bind(this);
    this.toggleCompleted = this.toggleCompleted.bind(this);
    this.clearCompleted = this.clearCompleted.bind(this);
    this.setView = this.setView.bind(this);
    this.toggleMode = this.toggleMode.bind(this);
  }

  // define the methods

  // update the current item with the text typed in the input field with the key as the current time
  handleInput(e) {
    this.setState({
      currentItem: {
        text: e.target.value,
        key: Date.now(),
        isCompleted: false
      }
    })
  }

  // the method that adds a new todo item to the list when the form is submitted
  addItem(e) {
    // prevent the page from refreshing by default on submit
    e.preventDefault();
    // grab the current item and store it in a variable
    const newItem = this.state.currentItem;
    console.log(newItem);
    // add the new item to the list if text is not empty and update state
    if (newItem.text !== "") {
      // a new array that includes the old items unpacked and the new item
      const newItems = [...this.state.items, newItem];
      // update state and clear the input field by setting currentItem to ''
      this.setState({
        items: newItems,
        currentItem: {
          text: '',
          key: '',
          isCompleted: false
        }
      })
    }
  }

  // a method to delete a single todo item when the cross is clicked
  deleteItem(key) {
    /* filter the items list that is saved in state => 
    leave all items except the one with the key that needs deleting */
    const filteredItems = this.state.items.filter(item =>
      item.key !== key);
    // update the state with the new filtered items list
    this.setState({
      items: filteredItems
    })
  }
  
  // a method to toggle the boolean value of isCompleted for the items in state
  toggleCompleted(key) {
    // find the item with the given key and toggle isCompleted on it
    const toggledItems = this.state.items.map(item => {
      if (item.key == key) {
        const toggledItem = item;
        if (!item.isCompleted) {
          toggledItem.isCompleted = true;
        } else {
          toggledItem.isCompleted = false;
        }
        return toggledItem;
      } else {
        return item;
      }
    });
    this.setState({
      items: toggledItems
    })
  }

  // a method to clear all completed todos
  clearCompleted() {
    /* filter the items list that is saved in state => 
    leave only items where isCompleted is false */
    const filteredItems = this.state.items.filter(item =>
      !item.isCompleted);
    // update the state with the new filtered items list
    this.setState({
      items: filteredItems
    })
  }

  // a method to change the which items are being viewed from the list
  setView(e) {
    this.setState({
      view: e.target.id
    })
  }

  // a method to toggle light and dark mode
  toggleMode() {
    this.setState(prevState => ({ isDark: !prevState.isDark }));
  }

  render() {
    return (
      <div className={`light_bg ${this.state.isDark ? "dark_bg" : ""}`}>
        <div id="app_container">
          <h1>TODO</h1>
          <div className={`switch_to_dark ${this.state.isDark ? "switch_to_light" : ""}`} 
          onClick={this.toggleMode}></div>
          <form 
              id="input_field" type="text" 
              // when the form is submitted (by pressing enter) we add the new item
              onSubmit={this.addItem}>
              <div className={`light_input_circle ${this.state.isDark ? "dark_input_circle" : ""}`}></div>
              <input type="text" placeholder="Create a new todo..."
                  className={`light_input ${this.state.isDark ? "dark_input" : ""}`}
                  /* link the value of the input field to the current item saved in state
                  and add an event handler to update state each time a letter is typed */
                  value={this.state.currentItem.text}
                  onChange={this.handleInput}>
              </input>
              <button type="submit" hidden></button>
          </form>
          <ListItems 
              // pass the state variables to the ListItems component as props
              // pass the required functions as props
              items={this.state.items}
              view={this.state.view}
              isDark={this.state.isDark}
              deleteItem={this.deleteItem}
              toggleCompleted={this.toggleCompleted}>
          </ListItems>
          <div className={`light_footer ${this.state.isDark ? "dark_footer" : ""}`}>
            <p 
                className={`light_counter ${this.state.isDark ? "dark_counter" : ""}`}>
                {this.state.items.length} items left
            </p>
            <nav>
              <ul>
                <li id="all" 
                    className={`light_filter ${this.state.isDark ? "dark_filter" : ""}`}
                    onClick={this.setView}
                    className={this.state.view == "all" ? "selected_filter" : ""}>
                    All
                </li>
                <li id="active" 
                    className={`light_filter ${this.state.isDark ? "dark_filter" : ""}`}
                    onClick={this.setView}
                    className={this.state.view == "active" ? "selected_filter" : ""}>
                    Active
                </li>
                <li id="completed"
                    className={`light_filter ${this.state.isDark ? "dark_filter" : ""}`}
                    onClick={this.setView}
                    className={this.state.view == "completed" ? "selected_filter" : ""}>
                    Completed
                </li>
              </ul>
            </nav>
            <button
                onClick={this.clearCompleted}
                className={`light_clear ${this.state.isDark ? "dark_clear" : ""}`}>
                Clear Completed
            </button>
          </div>
          <p 
              className={`light_drag ${this.state.isDark ? "dark_drag" : ""}`}>
              Drag and drop to reorder list
          </p>
        </div>
      </div>
    );
  }
}

export default App;
